<template>

    <div :style="style">
        <div style="margin-bottom: 25px">
            <i class="el-icon-plus"></i><span>添加</span>
            <div style="margin-top:10px;">
            <el-button type="primary" @click="addStudent()" plain>添加</el-button>
            </div>
        </div>
        <div>
            <div>
                <i class="el-icon-search"></i><span>查询</span>
                <div class='searchContent' style="margin-top:10px;">
                    <el-form :model="searchData" inline>
                        <el-form-item label="学号:">
                            <el-input v-model.trim="searchData.studentId"></el-input>
                        </el-form-item>
                        <el-form-item label="姓名:">
                            <el-input v-model.trim="searchData.studentName"></el-input>
                        </el-form-item>
                        <el-form-item label="专业名称:">
                            <el-input v-model.trim="searchData.majorName"></el-input>
                        </el-form-item>
                        <el-form-item label="学院名称:">
                            <el-input v-model.trim="searchData.departmentName"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" plain>查询</el-button>
                            <el-button type="success" @click="examineAll" plain>重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div>
                <div>
                    <i class="el-icon-document"><span>学生列表</span></i>
                </div>
            </div>

        </div>
        <el-table
                :data="studentList"
                style="width: 100%"
                height="100%">
            <el-table-column
                    fixed
                    prop="studentId"
                    label="学号"
                    >
            </el-table-column>
            <el-table-column
                    fixed
                    prop="studentName"
                    label="姓名"
            >
            </el-table-column>
            <el-table-column
                    prop="studentSex"
                    label="性别"
                    >
            </el-table-column>
            <el-table-column
                    prop="grade"
                    label="年级"
                    >
            </el-table-column>
            <el-table-column
                    prop="classId"
                    label="班级号"
                    >
            </el-table-column>
            <el-table-column
                    prop="majorName"
                    label="专业名称"
                    >
            </el-table-column>
            <el-table-column
                    prop="departmentName"
                    label="学院"
                    >
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    >
                <template slot-scope="scope">
                    <el-button @click="updateStudent(scope.row)" type="text" size="small">修改</el-button>
                    <el-button type="text" size="small" @click="deleteStudent(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                @current-change="handlePageChange"
                :total="pageInfo.total"
                :page-size="pageInfo.pageSize"
                :current-page="pageInfo.pageNum">
        </el-pagination>
        <transition name="fade" mode="out-in">
            <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible"
                       v-loading="loading"
                       element-loading-text="拼命加载中"
                       element-loading-spinner="el-icon-loading"
                       element-loading-background="rgba(0, 0, 0, 0.8)">
                <div v-if="dialogTitle === '修改信息'">
                <el-form :model="student" >
                    <el-form-item label="学号" :label-width="formLabelWidth">
                        <el-input v-model="student.studentId" autocomplete="on" size="normal" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" :label-width="formLabelWidth">
                        <el-input v-model="student.studentName" autocomplete="on" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="性别" :label-width="formLabelWidth">
                        <el-radio-group v-model="student.studentSex" size="small" disabled>
                            <el-radio label="男" border>男</el-radio>
                            <el-radio label="女" border>女</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="年级" :label-width="formLabelWidth">
                        <el-input v-model="student.grade" autocomplete="on"></el-input>
                    </el-form-item>
                    <el-form-item label="班级号" :label-width="formLabelWidth">
                        <el-input v-model="student.classId" autocomplete="on"></el-input>
                    </el-form-item>
                    <el-form-item label="专业-学院" :label-width="formLabelWidth">
                        <el-cascader
                                v-model="cascaderValue"
                                :options="cascaderOptions"
                                placeholder="如不修改则无需选择"
                                :props="{ expandTrigger: 'hover' }"
                                @change="majorDepartmentInfo()"></el-cascader>
                    </el-form-item>
                </el-form>
                </div>
                <div v-else>
                    <el-form :model="student" >
                        <el-form-item label="学号" :label-width="formLabelWidth">
                            <el-input v-model="student.studentId" autocomplete="on" size="normal"></el-input>
                        </el-form-item>
                        <el-form-item label="姓名" :label-width="formLabelWidth">
                            <el-input v-model="student.studentName" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="性别" :label-width="formLabelWidth">
                            <el-radio-group v-model="student.studentSex" size="small">
                                <el-radio label="男" border>男</el-radio>
                                <el-radio label="女" border>女</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="年级" :label-width="formLabelWidth">
                            <el-input v-model="student.grade" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="班级号" :label-width="formLabelWidth">
                            <el-input v-model="student.classId" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="专业-学院" :label-width="formLabelWidth">
                            <el-cascader
                                    v-model="cascaderValue"
                                    :options="cascaderOptions"
                                    placeholder="请选择"
                                    :props="{ expandTrigger: 'hover' }"
                                    @change="majorDepartmentInfo()"></el-cascader>
                        </el-form-item>
                    </el-form>
                </div>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="commitMessage()">确 定</el-button>
                </div>
            </el-dialog>
        </transition>
    </div>

</template>

<script>
// import axios from 'axios'
export default {
  name: 'page',
  data () {
    return {
      loading: false,
      cascaderValue: ['hehe', 'hah'], // 级联框的选择结果
      cascaderOptions: [],
      style: {
        height: window.innerHeight + 'px'
      },
      dialogTitle: '',
      dialogFormVisible: false,
      formLabelWidth: '120px',
      studentList: [
        {
          studentId: '1',
          studentName: '老何', // 学生姓名
          studentSex: '女', // 性别
          grade: '2013', // 年级
          classId: '01', // 班级ID
          majorId: '02', // 专业ID
          majorName: '计算机', // 专业名称
          departmentId: '03', // 学院ID
          departmentName: '计算机与信息' // 学院名称
        },
        {
          studentId: '2',
          studentName: '张三', // 学生姓名
          studentSex: '男', // 性别
          grade: '2013', // 年级
          classId: '02', // 班级ID
          majorId: '02', // 专业ID
          majorName: '美术', // 专业名称
          departmentId: '02', // 学院ID
          departmentName: '美术学院' // 学院名称
        }
      ],

      student: {
        studentId: '', // 学生ID
        studentName: '', // 学生姓名
        studentSex: '', // 性别
        grade: '', // 年级
        classId: '', // 班级ID
        majorId: '', // 专业ID
        majorName: '', // 专业名称
        departmentId: '', // 学院ID
        departmentName: '' // 学院名称
      },
      searchData: { // 搜索的数据
        studentId: '',
        majorName: '',
        studentName: '',
        departmentName: ''
      },
      pageInfo: {
        total: 100,
        pageSize: 15,
        pageNum: 1
      }
    }
  },
  methods: {
    /**
     * 修改页数
     */
    handlePageChange (val) {
      this.pageInfo.pageNum = val
      this.getStudentList()
    },
    /**
     * 添加学生
     */
    addStudent () {
      this.dialogFormVisible = true
      this.student = {}
      this.dialogTitle = '添加学生'
    },
    /**
     * 提交参数
     */
    commitMessage () {
      if (this.dialogTitle === '修改信息') {
        this.loading = true
        let data = this.student
        let callback = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('成功', 'success', '修改成功')
        }
        let error = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('警告', 'warning', '修改失败')
        }
        let exception = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('错误', 'error', '请求错误')
        }
        this._ajax('post', '/student/management/update', callback, {}, error, exception, data)
      } else {
        this.loading = true
        let data = this.student

        let callback = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('成功', 'success', '添加成功')
        }
        let error = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('警告', 'warning', '添加失败')
        }
        let exception = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('错误', 'error', '请求错误')
        }
        this._ajax('post', '/student/management/add', callback, {}, error, exception, data)
      }
    },
    /**
     * 选择专业和学院信息
     * @param value
     */
    majorDepartmentInfo (value) {
      this.cascaderOptions.forEach(department => {
        if (department.value === this.cascaderValue[0]) {
          this.student.departmentId = department.id
          department.children.forEach(major => {
            if (major.value === this.cascaderValue[1]) {
              this.student.majorId = major.id
            }
          })
        }
      })
      this.student.departmentName = this.cascaderValue[0]
      this.student.majorName = this.cascaderValue[1]
      console.log(this.cascaderValue)
    },
    /**
     * 更新学生信息
     * @param student 学生
     */
    updateStudent (student) {
      this.dialogFormVisible = true
      this.student = student
      this.dialogTitle = '修改信息'
      this.cascaderValue.splice(0, this.cascaderValue.length)
    },
    /**
     * 删除学生
     * @param student 学生
     */
    deleteStudent (student) {
      let callback = () => {
        let param = student.studentId
        let callback = (res) => {
          console.log(res)
          this._alert('删除成功！', 'success', 2000)
          this.getStudentList()
        }

        let error = (res) => {
          console.log(res)
        }
        let headers = {
          'Content-Type': 'application/x-www-form-urlencoded'
        }

        this._ajax('post', '/student/management/delete', callback, {id: param}, error, () => {}, headers)
      }
      let error = () => {
        this._alert('已取消', 'info', 2000)
      }
      this._confirm('您确定删除它吗？', callback, error)
    },
    /**
     * 搜索学生
     */
    search () {
      if (this.searchData.studentId === '' && this.searchData.studentName === '' &&
        this.searchData.majorName === '' && this.searchData.departmentName === '') {
        this.getStudentList()
      } else {
        let params = this.searchData
        let callback = (res) => {
          console.log(res)
          this.studentList = res.charData
        }
        let error = (res) => {
          console.log(res)
        }
        this._ajax('post', '/student/management/search', callback, {}, error, () => {}, params)
      }
    },
    /**
     * 重置查询内容
     */
    examineAll () {
      this.searchData = {}
    },
    getStudentList () {
      let callback = (res) => {
        this.studentList = res.charData.list
        this.pageInfo.pageSize = res.charData.pageSize
        this.pageInfo.total = res.charData.total
      }
      let error = (res) => {
        console.log(res)
      }
      let param = this.pageInfo
      this._ajax('get', '/student/management/list', callback, param, error, () => {})
    },
    getMajorDepartmentInfo () {
      let callback = (res) => {
        this.cascaderOptions = res.charData
      }
      let error = (res) => {
        console.log(res)
      }
      this._ajax('get', '/major/management/webList', callback, {}, error, () => {})
    }
  },
  mounted () {
    console.log(this.platform)
    this.getStudentList()
    this.getMajorDepartmentInfo()
  }
}
</script>

<style scoped>
    .btnFun {
        text-align: center;
        margin: 50px;
    }
    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    .bounce-leave-active {
        animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }
</style>



